import React from "react";

export default function Dropdown(props) {
    var totalPlayer = props.gk.length + props.st.length + props.md.length + props.df.length;
    function handleChoice(e) {
        props.roleSelect(e.target.value);
        if (totalPlayer >=6) {
            props.clearSelected()
        }
    }
    return (
        <div className="relative inline-flex">
            <svg className="w-2 h-2 absolute top-0 right-0 m-4 pointer-events-none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 412 232"><path d="M206 171.144L42.678 7.822c-9.763-9.763-25.592-9.763-35.355 0-9.763 9.764-9.763 25.592 0 35.355l181 181c4.88 4.882 11.279 7.323 17.677 7.323s12.796-2.441 17.678-7.322l181-181c9.763-9.764 9.763-25.592 0-35.355-9.763-9.763-25.592-9.763-35.355 0L206 171.144z" fill="#648299" fillRule="nonzero" /></svg>
            <select onChange={(e) => handleChoice(e)} className="border border-gray-800 rounded-full text-gray-600 h-10 pl-5 pr-10 bg-white hover:border-gray-400 focus:outline-none appearance-none">
                <option>Choose a position</option>
                {props.gk.length < 1 ? <option>Goalkeeper</option> : null}
                {((props.md.length !== 0 && props.df.length !== 0 && props.gk.length !== 0 && totalPlayer < 6)) || props.st.length < 1 ? <option>Striker</option> : null}
                {((props.gk.length !== 0 && props.st.length !== 0 && props.df.length !== 0 && totalPlayer < 6)) || props.md.length < 1 ? <option>Midfielder</option> : null}
                {((props.gk.length !== 0 && props.st.length !== 0 && props.md.length !== 0 && totalPlayer < 6)) || props.df.length < 1 ? <option>Defender</option> : null}
            </select>
        </div>
    );
}
